html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body {
    height: 100%;
    font-family: $primaryFont;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.wrapper {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
}

// LINK Cleanup
a, main li{
    text-decoration: none;
    list-style: none;
}


// FONTS
// Landing Page
h1, .hContainer p { 
    color: $secondaryColor;
} 
h1 {
    font-family: $primaryFont;
    font-size: calc(2.3vw + 2.7rem);
    text-transform: uppercase;
}
.hContainer p {
    font-family: $secondaryFont;
    font-size: calc((2.6vw + 2.4rem) * .6);
    text-transform: lowercase;
        &::first-letter{
            text-transform: uppercase;
        }
}


// MAIN Page
main h1{
    color: $secondaryColor;
    margin: 0;
    padding: 2% 5% 0 5%;
}
main p{
    font-family: $secondaryFont;
    font-size: calc((2.6vw + 2.4rem) * .28);
    text-align: left;
}
// NOTE: list items are buttons !!
button, 
form{
    background-color: $secondaryColor;
    border: none;
    font-family: $secondaryFont;
    font-size: calc((.5vw + 1rem));
    text-transform: uppercase;
    padding: calc((.4vw + 1.1rem) * .2) calc((.4vw + 1.1rem) * 1);
}


// Search-Main Page
form {
    display: flex;
}
main input {
    color: $primaryColor;
    text-align: center;
    padding: 0 calc((.4vw + 1.1rem) * 1); //width of input
    padding-top: calc((.4vw + 1.1rem) * .25);
}
.mButton{
    background-color: $primaryColor;
    color: $secondaryColor;
    // width: 100%;
}


// List Items
main button{
    font-size: calc((.3vw + 1.1rem));
    text-transform: capitalize;
    text-align: center;
}
